<template>
  <div class="container">
    <h2 class="text-center">題庫管理-題組修改</h2>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.go(-1)">返回上一頁</button>
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <div class="row">
      <div class="col-12">
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="questionGroupIDInput" v-model="questionGroup.QGID" placeholder="題組號" disabled />
          <label for="questionGroupIDInput">題組號</label>
        </div>
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="questionGroupNameInput" v-model="questionGroup.subject" placeholder="題組名" disabled />
          <label for="questionGroupNameInput">題組名</label>
        </div>
        <select class="form-select form-select-lg mb-3" aria-label=".form-select-lg example" v-model="questionGroup.status">
          <option value="true">上線</option>
          <option value="false">停用</option>
        </select>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-primary btn-lg" type="button" @click="sendEdit()">執行</button>
      </div>
    </div>
    <div class="row">
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-danger btn-lg" type="button" @click="deleteQuestion()">刪除題組</button>
      </div>
    </div>
  </div>
</template>

<script>
import router from "@/router";
import Alert from "@/components/Alert.vue";

export default {
  name: "QuestionEdit",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: 0,
      questionGroup: {
        QGID: "",
        subject: "",
        status: "",
      },
    };
  },
  methods: {
    sendEdit() {
      // 將questionGroup.status轉換為布林值
      this.questionGroup.status = this.questionGroup.status === "true" ? true : false;
      this.$http
        .post("api/questionManage/modifyQuestionGroup", this.questionGroup)
        .then((response) => {
          // 訊息提示框
          this.alertVisible = true;
          this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
          this.funcNum = 1;
          this.MSG.TYPE = "primary"; // primary成功, warning警告, danger錯誤
          this.MSG.TITLE = "系統提示"; // 系統提示，系統警告，系統錯誤
          this.MSG.TEXT = ["已修改完成！"]; // 內容
        })
        .catch((error) => {
          console.log(error);
        });
    },
    deleteQuestion() {
      this.alertVisible = true;
      this.MSG.MODE = "OKCancel"; // 形式為：OKOnly、OKCancel
      this.funcNum = 2;
      this.MSG.TYPE = "danger"; // primary成功, warning警告, danger錯誤
      this.MSG.TITLE = "系統警告"; // 系統提示，系統警告，系統錯誤
      this.MSG.TEXT = ["是否要刪除題庫？"]; // 內容
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  created() {
    // 載入資料
    this.questionGroup = this.$store.state.questGroupEdit;
  },
  mounted() {
    // 清除Vuex資料
    this.$store.commit("SET_QUEST_GROUP_EDIT", {});
  },
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === 1) {
            // 功能1-[修改題組-後續處理]
            // 頁面處理
            this.hideAlert();
            this.$router.go(-1);
          } else if (this.funcNum === 2) {
            // 功能2-[刪除題組]
            this.hideAlert();
            this.$http
              .post("api/questionManage/deleteQuestionGroup", this.questionGroup)
              .then((response) => {
                // 訊息提示框
                this.alertVisible = true;
                this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
                this.funcNum = 3;
                this.MSG.TYPE = "primary"; // primary成功, warning警告, danger錯誤
                this.MSG.TITLE = "系統提示"; // 系統提示，系統警告，系統錯誤
                this.MSG.TEXT = ["已刪除完成！"]; // 內容
              })
              .catch((error) => {
                console.log(error);
              });
          } else if (this.funcNum === 3) {
            // 功能2-[刪除題組-後續處理]
            // 頁面處理
            this.hideAlert();
            this.$router.go(-1);
          }
        } else if (val.OKorCancel === "Cancel") {
          this.$store.commit("SET_ALERT_VISIBLE", false);
          this.alertCheck = false;
        }
      }
    },
  },
};
</script>

<style lang="scss"></style>
